import { createStore } from 'vuex'

export default createStore({
  state: {
    //用户信息
    userInfo:{ },
  },
  mutations: {
    //更改用户信息
    loginSuccess(state,userInfo){
      state.userInfo = userInfo
    },
    //退出登录
    loginOut(state){
      state.userInfo = {}
    }
  },
  actions: {
    //...r 可以查看参数都有哪些
  },
  modules: {
  }
})
